// 믹스인 선언 등록
// 섹션3, 세션5 타이틀 
@mixin title(){
    width: 100%;
    padding-bottom: 60px;
    ul {
        width: 100%;
        li {
            width: 100%;
            text-align: center;
            i {                                    
                display: inline-block;
                width: 1px;
                height: 25px;
                background: #d00;
            }
            h2 {
                font-size: 23px;
                color: #232323;
                font-weight: 600;
                padding: 10px 0;
            }
            p {
                font-size: 14px;
                color: #888;
                line-height: 140%;
            }
        }
    }
}  

#wrap {
    #main {
       .section {
            width: 100%;
            .container{
                width: 100%;
                .title {
                    width: 100%;
                }
                .content {
                    width: 100%;
                }
            }
            &#section3{
                width: 100%;
                padding-top: 100px;
                overflow: hidden;  // 패럴럭스 구현 반드시 오버플로우 히든
                .container {
                    width: 100%;
                    .title {
                        // 믹스인 타이틀 가져오기
                        @include title();
                    }
                    .content {
                        width: 100%;
                        height: auto;
                         ul {
                            width: 100%;
                            display: flex;
                            flex-flow: row wrap; // 줄바굼 허용
                            padding: 0 10px;
                            li { // 10개
                                width: calc(100% / 5); // row  wrap 에서 적용                               
                                .gap {
                                    width: 100%;
                                    padding: 10px;
                                    padding: 0 10px 20px 10px;
                                    .img-box {
                                        width: 100%;
                                        position: relative;
                                        overflow: hidden;
                                        a {
                                            display: block;
                                            width: 100%;
                                            position: relative;
                                            z-index: 1;
                                            img {
                                                width: 100%;
                                                &:nth-child(1){  // 앞면 이미지
                                                    z-index: 1;
                                                    position: relative;
                                                }
                                                &:nth-child(2){  // 뒷면 이미지
                                                    z-index: 2;
                                                    position: absolute;
                                                    top: 0;
                                                    left: 0;
                                                    opacity: 0;
                                                    transition: opacity 0.6s;
                                                }
                                            }
                                        }

                                        .cart-box {
                                            z-index: 2;
                                            position: absolute;
                                            bottom: -40px;
                                            left: 0;
                                            width: 100%;
                                            height: 40px;                                            
                                            transition: all 0.2s ease-in;
                                            display: flex;
                                            button {
                                                width:50%;
                                                height: 100%;
                                                background: #000;
                                                border: 0;
                                                cursor: pointer;
                                                overflow: hidden;
                                                span {
                                                    width: 100%;
                                                    height: 100%;
                                                    display: flex;
                                                    align-items: center;
                                                    justify-content: center;
                                                    transition: all 0.2s ease-in-out;
                                                    &:nth-child(2){
                                                        font-size: 12px;                                                        
                                                        color: #111;
                                                    }
                                                    i {
                                                        font-size: 14px;
                                                        color:#fff;
                                                    }
                                                }

                                                &:hover { // 버튼
                                                    background: #fff;
                                                    span {
                                                        transform: translateY(-40px);
                                                        i { // 아이콘
                                                            color:#000;
                                                        }
                                                    }
                                                }
                                            }
                                        }
                                    }
                                    .caption-box {
                                        width: 100%;
                                        padding: 25px 0;
                                        text-align: center;
                                        font-size: 14px;
                                        line-height: 150%;
                                        a {
                                            color:#232323;
                                            font-weight: 600;
                                            transition: color 0.4s;
                                            display: block;
                                            width: 100%;
                                            &:hover{
                                                color: #d33;
                                            }
                                        }
                                        strong {
                                            color:#808080;
                                        }
                                    }
                                }
                                &:nth-child(6),
                                &:nth-child(7),
                                &:nth-child(8),
                                &:nth-child(9),
                                &:nth-child(10){
                                    .gap {
                                        padding-bottom: 0;
                                    }
                                }

                                &:hover {  // li 태그에 마우스 올리면
                                    .gap {
                                        .img-box {
                                            a {
                                                img {
                                                    &:nth-child(2){
                                                        opacity: 1;
                                                    }
                                                }
                                            }
                                            .cart-box {
                                                bottom: 0;
                                            }
                                        }
                                    }
                                }


                            }

                            @media all and (max-width: 1500px) {
                                li {
                                    width: calc(100% / 3);
                                }
                            }
                            @media all and (max-width: 1280px) {
                                li {
                                    width: calc(100% / 2);
                                }
                            }
                            @media all and (max-width: 780px) {
                                li {
                                    width: calc(100% / 1);
                                }
                            }
                        }
                    }
                }
                &.on {
                    .container {
                        .title {
                            animation: section2Title 4.8s forwards ease-in-out;
                        }
                        @keyframes section2Title {
                              0% {opacity: 0;}
                            100% {opacity: 1;}
                        }
                        .content {
                            ul {
                                li {
                                    &:nth-child(1){
                                        animation: section2Col1 2.0s forwards ease-in-out;
                                    }
                                    &:nth-child(2){
                                        animation: section2Col2 2.2s forwards ease-in-out;                                        
                                    }
                                    &:nth-child(3){
                                        animation: section2Col3 2.4s forwards ease-in-out;
                                    }
                                    &:nth-child(4){
                                        animation: section2Col4 2.6s forwards ease-in-out;
                                    }
                                    &:nth-child(5){
                                        animation: section2Col5 3.8s forwards ease-in-out;
                                    }
                                    &:nth-child(6){
                                        animation: section2Col6 4.0s forwards ease-in-out;   
                                    }
                                    &:nth-child(7){
                                        animation: section2Col7 4.2s forwards ease-in-out;
                                    }
                                    &:nth-child(8){
                                        animation: section2Col8 4.4s forwards ease-in-out;
                                    }
                                    &:nth-child(9){
                                        animation: section2Col9 4.6s forwards ease-in-out;
                                    }
                                    &:nth-child(10){
                                        animation: section2Col10 4.8s forwards ease-in-out;
                                    }
                                }

                                @keyframes section2Col1 {
                                      0% {transform: translateY(1000px);}
                                    100% {transform: translateY(   0px);}
                                }
                                @keyframes section2Col2 {
                                      0% {transform: translateY(1000px);}
                                    100% {transform: translateY(   0px);}
                                }
                                @keyframes section2Col3 {
                                      0% {transform: translateY(1000px);}
                                    100% {transform: translateY(   0px);}
                                }
                                @keyframes section2Col4 {
                                      0% {transform: translateY(1000px);}
                                    100% {transform: translateY(   0px);}
                                }
                                @keyframes section2Col5 {
                                      0% {transform: translateY(1000px);}
                                    100% {transform: translateY(   0px);}
                                }
                                @keyframes section2Col6 {
                                      0% {transform: translateY(1000px);}
                                    100% {transform: translateY(   0px);}
                                }
                                @keyframes section2Col7 {
                                      0% {transform: translateY(1000px);}
                                    100% {transform: translateY(   0px);}
                                }
                                @keyframes section2Col8 {
                                      0% {transform: translateY(1000px);}
                                    100% {transform: translateY(   0px);}
                                }
                                @keyframes section2Col9 {
                                      0% {transform: translateY(1000px);}
                                    100% {transform: translateY(   0px);}
                                }
                                @keyframes section2Col10 {
                                      0% {transform: translateY(1000px);}
                                    100% {transform: translateY(   0px);}
                                }
                            }
                        }
                    }
                }
            }
        }
    }
}