#wrap {
    #main {
       .section {
            width: 100%;
            .container{
                width: 100%;
                .title {
                    width: 100%;
                }
                .content {
                    width: 100%;
                }
            }
            &#section4{
                width: 100%;
                padding-top: 100px;
                 overflow: hidden;  // 패럴럭스 구현 반드시 오버플로우 히든
                .container {
                    width: 100%;
                    .content {
                        width: 100%;
                        display: flex;
                        flex-flow: row wrap;
                        padding: 0 7.5px;
                        .left{
                            width: 50%;
                            .gap {
                                width: 100%;
                                padding: 0 7.5px;
                                position: relative;
                                img {
                                   width: 100%; 
                                   position: relative;
                                   z-index: 1;
                                }
                                .title {
                                    z-index: 2;
                                    position: absolute;
                                    width: 100%;
                                    height: 100%;
                                    top: 0;
                                    left: 0;                                 
                                    color:#fff;
                                    // background-color: #d00;
                                    padding: 0 7.5px;

                                    display: flex;
                                    // align-items: center;          // 상하 가운데   
                                    // align-items: flex-end;        // 아래쪽
                                    // align-items: flex-start;      // 위쪽
                                    // justify-content: center;      // 좌우 가운데
                                    // justify-content: flex-end;    // 오른쪽
                                    // justify-content: flex-start;  // 왼쪽
                                    ul {
                                        // margin: auto;                // 상하좌우 가운데 정렬
                                        // margin: auto auto auto 15%;  // 좌측정렬
                                        // margin: 5% auto auto auto;  // 위쪽 좌우 가운데정렬
                                        // margin: auto auto 5% auto;  // 아래쪽 좌우 가운데정렬
                                        margin: auto 15% auto auto;  // 우측정렬
                                        li {
                                            h2 {
                                                color: #e2d9d6;
                                                font-size: 14px;
                                            }
                                            h3 {
                                                color: #fff;
                                                font-size: 65px;
                                                font-weight: 600;
                                                padding: 10px 0;
                                            }
                                            a {
                                                color: #fff;
                                                font-size: 10px;
                                                font-weight: 600;
                                                padding: 7px 20px;
                                                background: #000;
                                                display: inline-block;
                                                border-radius: 2px;
                                            }
                                        }
                                    }
                                }
                            }
                        }
                        .right{
                            width: 50%;
                            .gap {
                                width: 100%;
                                padding: 0 7.5px;
                                position: relative;
                                img {
                                    width: 100%;
                                   position: relative;
                                   z-index: 1;
                                }
                                .title {

                                    z-index: 2;
                                    position: absolute;
                                    top:0;
                                    left: 7.5px;

                                    width: 100%;
                                    height: 100%;

                                    display: flex;
                                    // align-items: center;
                                    padding: 0 7.5px;
                                    ul {
                                        width:100%;
                                        margin: auto;    // 상하좌우 가운데                               
                                        li {
                                            width:100%;
                                            text-align: center;
                                            h2 {
                                                color: #fff;
                                                font-size: 65px;
                                                font-weight: 600;
                                            }
                                            h3 {
                                                color: #fff;
                                                font-size: 11px;
                                                font-weight: 600;
                                                background: #000;
                                                padding: 7px 20px;
                                                display: inline-block;
                                                margin: 2%  0;
                                            }
                                            h4 {
                                                font-size: 30px;
                                                font-weight: 300;
                                                color: #fff;
                                                line-height: 120%;
                                                padding-bottom: 7%; // % = 패딩값 / 너비
                                            }    
                                            form {
                                                width: 60%;
                                                margin: 0 auto;                                             
                                                .email-box {
                                                    width: 100%;
                                                    border-bottom: 1px solid #000;
                                                    position: relative;
                                                    input[type='email'] {
                                                        width: 100%;
                                                        height: 40px;
                                                        background: transparent;
                                                        border: 0;       
                                                         position: relative;
                                                         z-index: 1;   
                                                         color:#555;                                              
                                                        &:focus {
                                                            outline: 0;
                                                        }
                                                        &::placeholder {
                                                            color: #555;
                                                            opacity: 0.7;
                                                        }
                                                    }
                                                    button {
                                                        z-index: 2;
                                                        position: absolute;
                                                        top: 0;
                                                        right: 0;
                                                        height: 40px;
                                                        display: flex;
                                                        align-items: center;
                                                        background: transparent;
                                                        border: 0;
                                                        cursor: pointer;
                                                        transition: all 0.3s;
                                                        span {
                                                            color: #444;
                                                        }
                                                        i {
                                                            color: #444;
                                                            font-size: 21px;
                                                        }
                                                        &:hover {
                                                            span {
                                                                color: #000;
                                                            }
                                                            i {
                                                                color: #000;
                                                            }
                                                        }
                                                    }
                                                }  
                                            }
                                        }
                                    }
                                }
                            }
                        }

                        @media all and (max-width: 1280px) {
                            .left {
                                 width: 100%;
                                  .gap {
                                    padding: 7.5px;
                                  }
                            }
                            .right {
                                width: 100%;
                                .gap {
                                    padding: 7.5px;
                                }
                            }
                        }

                         @media all and (max-width: 700px) {
                            .left {
                                .gap {
                                    .title {
                                    ul {
                                        li {
                                            h3{
                                                font-size: 40px;
                                            }
                                        }
                                    }
                                    }
                                }
                            }
                            .right {                                
                                .gap {
                                    .title {
                                        //  height: auto;
                                        ul {
                                            li {
                                                h2{
                                                    font-size: 40px;
                                                }
                                                h4{
                                                    font-size: 17px;
                                                    padding-bottom: 4%;
                                                }
                                            }
                                        }
                                    }
                                }
                            }
                        }

                        
                         @media all and (max-width: 400px) {
                            .left {
                                .gap {
                                    .title {
                                    ul {
                                        li {
                                            h3{
                                                font-size: 30px;
                                            }
                                        }
                                    }
                                    }
                                }
                            }
                            .right {                                
                                .gap {
                                    .title {
                                        ul {
                                            li {
                                                h2{
                                                    font-size: 30px;
                                                }
                                                h4{
                                                    font-size: 14px;
                                                    padding-bottom: 3%;
                                                }
                                            }
                                        }
                                    }
                                }
                            }
                        }
                    }
                }

                &.on {
                   .container {
                        .content {
                            .left {
                                animation: section3Col1 2.0s forwards ease-in-out;                                
                            }
                            .right {
                                animation: section3Col2 2.0s forwards ease-in-out;
                            }
                            @keyframes section3Col1 {
                                  0% {transform: translateX(-1000px);}
                                100% {transform: translateY(    0px);}
                            }
                            @keyframes section3Col2 {
                                  0% {transform: translateX( 1000px);}
                                100% {transform: translateY(    0px);}
                            }
                        }
                   } 
                }
            }
        }
    }
}