#wrap {
    #main {
       .section {
            width: 100%;
            .container{
                width: 100%;
                .title {
                    width: 100%;
                }
                .content {
                    width: 100%;
                }
            }
            &#section6{
                width: 100%;
                height: 654px;
                margin-top: 100px;
                background: url(../../../../../public/images/home-page-fashion-deal-day-bg.jpg) no-repeat 50% 50%;
                background-size: cover;
                overflow: hidden;
                .container {
                    width: 100%;
                    max-width: 1140px;
                    height: 100%;
                    margin: 0 auto;
                    .content {
                        width: 100%;
                        height: 100%;
                        display: flex;
                        align-items: center;
                        justify-content: center;
                        > ul {
                            width: 100%;
                            > li {
                                width: 100%;
                                text-align: center;
                                &:nth-child(1){
                                    padding-bottom: 10px;
                                }
                                &:nth-child(3){
                                    padding: 70px 0;
                                }
                                text-align: center;

                                h2 {
                                    font-size: 14px;
                                    font-weight: 500;
                                    color: #6f6f6f;
                                }
                                h3 {
                                    font-size: 45px;
                                    font-weight: 600;
                                    color: #000;
                                    text-transform: uppercase;
                                }
                                ul {
                                    display: inline-flex;
                                   
                                    // 너비 오토
                                    li {                                
                                       display: flex;
                                       align-items: center;
                                       position: relative;
                                       padding-bottom: 20px;  /* 하단 텍스트 공간 */
                                       width: 165px;
                                       
                                       &.icon {
                                        width: auto;
                                       }
                                        span {
                                            font-size: 55px;
                                            font-weight: 600;
                                            color: #000;
                                            width: 100%;
                                            &.days{
                                                width: 100%;
                                            }
                                        }
                                        i {
                                            font-size: 35px;
                                            font-weight: 500;
                                            color: #c00;
                                            // margin: 0 35px;
                                        }
                                        
                                        strong {
                                            position: absolute;
                                            bottom: 0;
                                            left: 0;
                                            width: 100%;
                                            display: block;
                                            color: #808080;
                                            font-size: 14px;
                                            padding-top: 5px;
                                            text-transform: uppercase;
                                        }
                                        
                                    }
                                }

                                @media all and (max-width: 680px) {
                                    h3 {
                                        font-size: 35px;
                                    }
                                    ul {
                                        width: 100%;
                                        flex-flow: row wrap;
                                        li { 
                                            width: calc(50% - 9px);            // : 너비 9픽셀
                                            margin: 20px 0;
                                            &.icon  {                                            
                                                &.icon-hide {
                                                    display: none;
                                                }
                                            }
                                            span {
                                                font-size: 40px;                                              
                                            }
                                            i {
                                                font-size: 30px;
                                            }
                                            
                                            
                                        }
                                    }
                                }
                                @media all and (max-width: 480px) {
                                    h2 {
                                        font-size: 11px;
                                    }                                    
                                    h3 {
                                        font-size: 30px;
                                    }
                                    ul {
                                        width: 100%;
                                        flex-flow: row wrap;
                                        li { 
                                            width: calc(50% - 5px);            // : 너비 9픽셀
                                            margin: 20px 0;
                                            &.icon  {                                            
                                                &.icon-hide {
                                                    display: none;
                                                }
                                            }
                                            span {
                                                font-size: 30px;                                              
                                            }
                                            i {
                                                font-size: 20px;
                                            }
                                            
                                            
                                        }
                                    }
                                }                                
                                a {
                                    font-size: 12px;
                                    font-weight: 600;
                                    padding: 10px 25px;
                                    background: #000;
                                    color: #fff;
                                    border: 2px solid #000;
                                    display: inline-block;
                                }
                            }
                        }
                    }
                }
                &.on {
                    .container {
                        .content {
                            > ul {
                                > li  {
                                    &:nth-child(1){
                                        animation: section6Col1 2.0s forwards ease-in-out;
                                    }
                                    &:nth-child(2){
                                        animation: section6Col2 2.2s forwards ease-in-out;
                                    }
                                    &:nth-child(3){
                                        animation: section6Col3 2.4s forwards ease-in-out;
                                    }
                                    &:nth-child(4){
                                        animation: section6Col4 2.6s forwards ease-in-out;
                                    }
                                }
                                @keyframes section6Col1 {
                                      0% {transform: translateY( 1000px);opacity: 0;}
                                    100% {transform: translateY(    0px);opacity: 1;}
                                }
                                @keyframes section6Col2 {
                                      0% {transform: translateY( 1000px);opacity: 0;}
                                    100% {transform: translateY(    0px);opacity: 1;}
                                }
                                @keyframes section6Col3 {
                                      0% {transform: translateY( 1000px);opacity: 0;}
                                    100% {transform: translateY(    0px);opacity: 1;}
                                }
                                @keyframes section6Col4 {
                                      0% {transform: translateY( 1000px);opacity: 0;}
                                    100% {transform: translateY(    0px);opacity: 1;}
                                }
                            }
                        }
                    }
                }
            }
        }
    }
}