#wrap {
    #main {
       .section {
            width: 100%;
            .container{
                width: 100%;
                .title {
                    width: 100%;
                }
                .content {
                    width: 100%;
                }
            }
            &#section8{
                padding: 100px 0;
                width: 100%;
                background: #f7f7f7;
                overflow: hidden;
                .container {
                    width: 100%;
                    max-width: 1150px;
                    margin: 0 auto;
                    .content {
                        width: 100%;
                        .slide-container {
                            width: 100%;
                            .slide-view {
                                width: 100%;
                                overflow: hidden;
                                .slide-wrap {
                                    width: 191.6666667px * 8;
                                    display: flex;
                                    position: relative;
                                    left: -191.6666667px * 0;
                                    left: -191.6666667px * 1;
                                    left: -191.6666667px * 2;  // 2보다 크면 리턴
                                    left: -191.6666667px * 0;
                                    .slide {
                                        width: 191.6666667px;                                        
                                        // pointer-events: none;
                                        a.gap {
                                            display: block;
                                            // width: auto;
                                            width: calc(100% - 10px);
                                            margin: 0 5px;                                            
                                            img {
                                                width: 100%;      
                                                transition: all 0.3s;                                                                                  
                                            }
                                            &:hover {
                                                img {
                                                    filter: brightness(25%);                                                                                  
                                                }  
                                            }
                                        }
                                    }
                                }
                            }
                        }
                    }
                }
                &.on {
                    .container {
                        .content {
                            .slide-container {
                                animation: section8Slider 1.0s forwards ease-in-out;
                            }
                            @keyframes section8Slider {
                                  0% {transform: translateX( -1000px);}
                                100% {transform: translateX(    0px);}
                            }
                        }
                    }
                }
            }
        }
    }
}