#wrap {
    #main {
       .section {
            width: 100%;
            .container{
                width: 100%;
                .title {
                    width: 100%;
                }
                .content {
                    width: 100%;
                }
            }
            &#section1{
                 width: 100%;
                 position: relative;                 
                .slide-container {
                     width: 100%;
                     position: relative;
                     z-index: 1;
                    .slide-view {
                         width: 100%;
                         overflow: hidden;
                         position: relative;
                         z-index: 1;
                        .slide-wrap {
                            width: 100% * 5;
                            display: flex;

                            margin-left: -100%;
                            position: relative;

                            // left: -100% * cnt;  //1번 슬라이드
                            left: -100% * 0;  //1번 슬라이드
                            left: -100% * 1;  //2번 슬라이드
                            left: -100% * 2;  //3번 슬라이드
                            left: -100% * 3;  //4번 슬라이드 첫번째
                            left: -100% * 0;  //1번 슬라이드 리턴

                            .slide {  // 3 + 2 = 5개
                                width: 100%;
                                height: 100%;
                                height: 100vh;
                                position: relative;
                                // pointer-events: none; 이미지 위치에 포인터 이벤트 사용안함
                                img {
                                    z-index: 1; 
                                    position: relative;
                                    width: 100%;
                                    height: 100%;
                                    object-fit: cover;
                                    pointer-events: none; 
                                }
                                .title {
                                    z-index: 2;
                                    position: absolute;
                                    left: 0;
                                    top: 0;
                                    width: 100%;
                                    height: 100%;
                                    div {
                                        width: 100%;
                                        max-width: 1140px;
                                        margin: 0 auto;
                                        height: 100%;
                                        display: flex;
                                        ul {
                                            margin: auto auto auto 0;
                                            li {
                                                h3 {
                                                    font-size: 12px;
                                                    font-weight: 500;
                                                } 
                                                h4 {
                                                    font-family: arial;
                                                    font-size: 100px;
                                                    font-weight: 800;
                                                    padding: 10px 0 25px 0;
                                                    line-height: 90%;
                                                    
                                                    // 텍스트 마스크                                                    
                                                    background: url(../../../../../public/images/infa-05.jpg) no-repeat 0 0;
                                                    background-size: cover;
                                                    // 텍스트에 배경 넣기
                                                    background-clip: text; // 반드시 텍스트롤 사용을 지정한다.
                                                    color: transparent;  // 반드시 텍스트는 투명하게 처리한다.
                                                    
                                                } 
                                               
                                                p {
                                                    font-size: 16px;
                                                    font-weight: 300;
                                                    padding: 0 0 40px 0;
                                                    line-height: 130%;
                                                    
                                                } 
                                                a {
                                                    font-size: 12px;
                                                    font-weight: 600;
                                                    padding: 10px 30px;
                                                    background: #000;
                                                    color: #fff;
                                                    border-radius: 18.5px;
                                                    border: 2px solid #000;
                                                    transition: all 0.3s;
                                                    &:hover {
                                                        background: transparent;
                                                        color: #000;
                                                    }
                                                } 
                                            }
                                        }
                                    }
                                }
                                &.on{
                                    .title {
                                        div {
                                            ul {
                                                li {
                                                    h4 { 
                                                        animation: mask-text 2s forwards ease-in-out;
                                                    }
                                                     @keyframes mask-text {
                                                          0%{background-position: 0   0;}
                                                        100%{background-position: 0 60%;}
                                                    }
                                                }
                                            }
                                        }
                                    }
                                }

                                @media all and (max-width: 1140px) {
                                    .title {
                                        padding: 0 50px;
                                        div {
                                            ul {
                                                li {
                                                    h4 {
                                                        font-size: 70px;
                                                    }
                                                }
                                            }
                                        }
                                    }
                                }
                                @media all and (max-width: 700px) {
                                    .title {
                                        div {
                                            ul {
                                                li {
                                                    h4 {
                                                        font-size: 50px;
                                                    }
                                                }
                                            }
                                        }
                                    }
                                }
                                @media all and (max-width: 500px) {
                                    .title {
                                        div {
                                            ul {
                                                li {
                                                    h4 {
                                                        font-size: 40px;
                                                    }
                                                }
                                            }
                                        }
                                    }
                                }
                            }
                        }
                    }

                    .count-box {                       
                        z-index: 2;
                        position: absolute;
                        top: 50%;
                        font-size: 18px;
                        margin-top: -9px;
                        font-weight: 600;
                        &.prev-box {
                            left: 50px;
                            a.prev-btn {
                                color: #888;
                                text-decoration: line-through;
                            }                           
                        }
                        &.next-box {
                            right: 50px;
                            a.next-btn {
                                color: #888;
                                text-decoration: line-through;
                            }    
                        }

                        @media all and (max-width: 1140px) {
                             &.prev-box {
                                left: 10px;
                                                        
                            }
                            &.next-box {
                                right: 10px;
                                 
                            }
                        }

                    }
                }

                .direct-box {
                    z-index: 2;
                    position: absolute;
                    bottom: 40px;
                    left: 50%;
                    margin-left: -20px;
                    background: transparent;
                    text-align: center;
                    width: 40px;
                    height: 40px;                    
                    a {
                        display: inline-flex;
                        width: 40px;
                        height: 40px;
                        background: #fff;  
                        align-items: center;
                        justify-content: center;   
                        border-radius: 50%;      
                        box-shadow: 0 0 10px rgb(0,0,0, 0.15); 
                        transition: all 0.3s ease-in-out;            
                        i {
                            color: #000; 
                            font-size: 20px;  
                            transition: all 0.3s ease-in-out;     
                        }
                        &:hover {
                            background: #f57250;
                            i {
                                color: #fff;
                            }
                        }
                    }
                }
            }
        }
    }
}