#wrap {
    #main {
       .section {
            width: 100%;
            .container{
                width: 100%;
                .title {
                    width: 100%;
                }
                .content {
                    width: 100%;
                }
            }
            &#section7{
                width: 100%;
                padding: 80px 0;
                overflow: hidden;
                .container {
                    width: 100%;
                    max-width: 1140px;
                    margin: 0 auto;
                    .content {
                        width:100%;
                       > ul {
                            width:100%;
                            display: flex;
                            justify-content: space-between;                            
                           > li {
                                width: 100% / 4;
                                text-align: center;
                                .gap {                                    
                                    i {
                                        font-size: 20px;
                                        color:#f57250;
                                    }
                                    span {
                                        font-size: 12px;
                                        color:#808080;
                                        margin-left: 15px;
                                    }
                                }
                            }
                        }


                        @media all and (max-width: 1200px) {
                            > ul {
                                flex-flow: row wrap;
                                 > li {
                                    width: calc(100% / 2);
                                    margin: 20px 0;
                                }
                            }
                        }
                        @media all and (max-width: 500px) {
                            > ul {
                                 > li {
                                    width: calc(100% / 1);
                                }
                            }
                        }

                    }
                }
                &.on {
                    .container {
                        .content {
                            > ul {
                                > li  {
                                    &:nth-child(1){
                                        animation: section7Col1 2.0s forwards ease-in-out;
                                    }
                                    &:nth-child(2){
                                        animation: section7Col2 2.2s forwards ease-in-out;
                                    }
                                    &:nth-child(3){
                                        animation: section7Col3 2.4s forwards ease-in-out;
                                    }
                                    &:nth-child(4){
                                        animation: section7Col4 2.6s forwards ease-in-out;
                                    }
                                }
                                @keyframes section7Col1 {
                                      0% {transform: translateX( 1000px);}
                                    100% {transform: translateX(    0px);}
                                }
                                @keyframes section7Col2 {
                                      0% {transform: translateX( 1000px);}
                                    100% {transform: translateX(    0px);}
                                }
                                @keyframes section7Col3 {
                                      0% {transform: translateX( 1000px);}
                                    100% {transform: translateX(    0px);}
                                }
                                @keyframes section7Col4 {
                                      0% {transform: translateX( 1000px);}
                                    100% {transform: translateX(    0px);}
                                }
                            }
                        }
                    }
                }
            }
        }
    }
}