#wrap {
    #main {
       .section {
            width: 100%;
            .container{
                width: 100%;
                .title {
                    width: 100%;
                }
                .content {
                    width: 100%;
                }
            }
            &#section2{
                width: 100%; 
                overflow: hidden;  // 패럴럭스 구현 반드시 오버플로우 히든
                .container {
                    width: 100%;
                    .content {
                        width: 100%;
                        // 임시높이 풀기
                        height: auto;
                        ul.grid-table {  
                            // 테이블
                            // 그리드(테이블 격자) 방식 칸 배치 
                            // 2행 4열 선언
                            display: grid;
                            grid-template-areas: 
                                "col1 col1 col2 col2"
                                "col3 col4 col2 col2"
                            ;
                            padding: 20px;
                            grid-gap: 20px;
                            width: 100%;
                            
                            li.col {  // 칸 
                                width: 100%;
                                height: 100%;
                                .gap {
                                    width: 100%;
                                    height: 100%;
                                    position: relative;
                                    img {
                                        width: 100%;
                                        height: 100%;
                                        object-fit: cover;
                                        z-index: 1;
                                        position: relative;
                                    }
                                    .title{
                                        z-index: 2;
                                        position: absolute;
                                        top:0;
                                        left:0;
                                        width: 100%;
                                        height: 100%;
                                        display: flex;
                                        ul {
                                            margin: auto;
                                            &.col1-ul {
                                                margin: auto 11% auto auto;  // 상하 가운데  오른쪽 정렬
                                            }
                                            &.col2-ul {
                                                margin: 10% 11% auto auto;   // 위쪽 오른쪽 정렬
                                            }
                                            &.col3-ul {
                                                margin: auto auto auto 11%;   // 상하가운데 왼쪽 정렬
                                            }
                                            li {
                                                h2 {
                                                    font-size: 23px;
                                                    color: #fff;
                                                    padding-bottom: 20px;
                                                    line-height: 120%;
                                                }
                                                a {
                                                    font-size: 12px;
                                                    font-weight: 500;
                                                    color: #fff;
                                                    display: inline-flex;
                                                    text-decoration: underline;
                                                    text-underline-offset: 4px;
                                                    transition: all 0.3s;
                                                    &:hover {
                                                        opacity: 0.6;
                                                    }
                                                }
                                            }
                                        }
                                    }
                                }
                                &.col1{
                                    grid-area: col1;                                    
                                }
                                &.col2{
                                    grid-area: col2;                                    
                                }
                                &.col3{
                                    grid-area: col3;                                    
                                }
                                &.col4{
                                    grid-area: col4;
                                }
                            }
                        }


                        @media all and (max-width: 1000px) {
                           ul.grid-table{
                                grid-template-areas: 
                                    "col1 col1" 
                                    "col2 col2"
                                    "col3 col4"
                                ;
                           }     
                        }
                        @media all and (max-width: 800px) {
                           ul.grid-table{
                                grid-template-areas: 
                                    "col1" 
                                    "col2"
                                    "col3"
                                    "col4"
                                ;
                           }     
                        }
                    }
                }

                &.on{
                    .container {
                        .content {
                            ul.grid-table {
                                li.col {
                                    &.col1{
                                        animation: section1Col1 2.0s forwards ease-in-out;
                                    }
                                    &.col2{
                                        animation: section1Col2 2.2s forwards ease-in-out;
                                    }
                                    &.col3{
                                        animation: section1Col3 2.4s forwards ease-in-out;
                                    }
                                    &.col4{
                                        animation: section1Col4 2.6s forwards ease-in-out;
                                    }
                                    @keyframes section1Col1 {
                                          0% {transform: translateY(1000px);}
                                        100% {transform: translateY(   0px);}
                                    }
                                    @keyframes section1Col2 {
                                          0% {transform: translateY(1000px);}                                        
                                        100% {transform: translateY(   0px);}
                                    }
                                    @keyframes section1Col3 {
                                          0% {transform: translateY(1000px);}                                        
                                        100% {transform: translateY(   0px);}
                                    }
                                    @keyframes section1Col4 {
                                          0% {transform: translateY(1000px);}                                        
                                        100% {transform: translateY(   0px);}
                                    }

                                }
                            }
                        }
                    }
                }
            }
        }
    }
}