// 믹스인 선언 등록
// 섹션3, 세션5 타이틀 
@mixin title(){
    width: 100%;
    padding-bottom: 60px;
    ul {
        width: 100%;
        li {
            width: 100%;
            text-align: center;
            i {                                    
                display: inline-block;
                width: 1px;
                height: 25px;
                background: #d00;
            }
            h2 {
                font-size: 23px;
                color: #232323;
                font-weight: 600;
                padding: 10px 0;
            }
            p {
                font-size: 14px;
                color: #888;
                line-height: 140%;
            }
        }
    }
}  
#wrap {
    #main {
       .section {
            width: 100%;
            .container{
                width: 100%;
                .title {
                    width: 100%;
                }
                .content {
                    width: 100%;
                }
            }
            &#section5{
                width: 100%;
                padding-top: 100px;
                overflow: hidden;
                .container {
                    width: 100%;
                    max-width: 1484.620px;
                    margin: 0 auto;
                    .title {
                        @include title();
                    }
                   .content {
                        width: 100%;
                        height: auto;
                        > ul {
                            width: 100%;
                            display: flex;
                            flex-flow: row wrap;
                            > li {
                                width: calc(100% / 4);
                                .gap {
                                    width: calc(100% - 20px);
                                    margin: 0 10px;
                                    position: relative;
                                    a.img-box {
                                        z-index: 1;
                                        position: relative;
                                        display: block;
                                        width: 100%;
                                        overflow: hidden;
                                        img {                                            
                                            width: 100%;
                                            transition: all 0.3s ease-in-out;
                                        }
                                    }
                                    .caption-box {
                                        z-index: 2;
                                        position: absolute;
                                        left: 0;
                                        bottom: 0;
                                        width: calc(100% - 60px);
                                        background: #000;
                                        margin: 30px;  
                                        transition: bottom 0.3s ease-in-out;                                     
                                        ul {
                                            width: 100%;
                                            text-align: center;
                                            padding-top: 20px;
                                            li {
                                                padding: 10px 0;
                                                i {
                                                    color: #808080;
                                                    margin: 0 10px;
                                                    font-size: 12px;
                                                }
                                                span {
                                                    font-size: 12px;
                                                    color: #808080;
                                                    a {
                                                        color: #808080;
                                                        transition: color 0.3s ease-in-out;
                                                        &:hover{
                                                           color: #c00; 
                                                        }
                                                    }
                                                }

                                                &:first-child {
                                                   a {
                                                        color: rgba(255,255,255, 0.6);
                                                        transition: color 0.3s;
                                                        &:hover {
                                                            color: rgba(255,255,255, 1);
                                                        }
                                                   } 
                                                }
                                                 &:nth-child(2) {
                                                    a {
                                                      color: rgba(255,255,255, 1);  
                                                      line-height: 130%;
                                                    }
                                                    padding-bottom: 30px;
                                                 }
                                                &:last-child {
                                                    background: #fff;
                                                    padding: 15px 0;
                                                }

                                            }
                                        }
                                    }

                                    &:hover {
                                        a.img-box {
                                            img {
                                                transform: scale(1.15);
                                                filter: brightness(0.65);
                                            }
                                        }
                                        .caption-box {
                                            bottom: 15px;
                                        }
                                    }
                                }
                            }

                            @media all and (max-width: 1280px) {
                                > li{
                                    width: calc(100% / 2);   
                                }
                            }
                            @media all and (max-width: 760px) {
                                > li{
                                    width: calc(100% / 1);   
                                }
                            }
                        }
                   } 
                }
                &.on {
                    .container {
                        .title {
                           animation: section5Title 2.0s forwards ease-in-out;
                        }
                        @keyframes section5Title {
                              0% {transform: translateY( 1000px);}
                            100% {transform: translateY(    0px);}
                        }
                        .content {
                            > ul {
                                > li  {
                                    &:nth-child(1){
                                        animation: section5Col1 2.2s forwards ease-in-out;
                                    }
                                    &:nth-child(2){
                                        animation: section5Col2 2.4s forwards ease-in-out;
                                    }
                                    &:nth-child(3){
                                        animation: section5Col3 2.6s forwards ease-in-out;
                                    }
                                    &:nth-child(4){
                                        animation: section5Col4 2.8s forwards ease-in-out;
                                    }
                                }
                                @keyframes section5Col1 {
                                    0% {transform: translateY( 1000px);}
                                    100% {transform: translateY(    0px);}
                                }
                                @keyframes section5Col2 {
                                    0% {transform: translateY( 1000px);}
                                    100% {transform: translateY(    0px);}
                                }
                                @keyframes section5Col3 {
                                    0% {transform: translateY( 1000px);}
                                    100% {transform: translateY(    0px);}
                                }
                                @keyframes section5Col4 {
                                    0% {transform: translateY( 1000px);}
                                    100% {transform: translateY(    0px);}
                                }
                            }
                        }
                    }
                }
            }
        }
    }
}