#wrap {
    #footer {
        z-index: 1;
        position: relative;
        width: 100%;
        .row {
            width: 100%;
            .container {
                width: 100%;
                max-width: 1140px;
                margin: 0 auto;                
            }
            &.row1{
                background: #1e1e1e;
                border-bottom: 1px solid #333; 
                padding: 30px 0;                            
                .container {
                    display: flex;  
                    .left {
                        width:50%;
                        display: flex;
                        align-items: center;
                        span {
                            color: #B7B7B7;
                            font-size: 12px;
                        }
                        form {
                            margin-left: 15px;
                            position: relative;
                            input {
                                position: relative;
                                z-index: 1;
                                background: #303030;
                                color:#8a8a8a;
                                font-size: 13px;
                                width: 350px;
                                height: 36px;
                                border: 0;
                                padding: 0 15px;
                            }
                            button {
                                position: absolute;
                                top:0;
                                right: 15px;
                                z-index: 1;
                                background:transparent;
                                color:#8a8a8a;
                                font-size: 12px;
                                height: 36px;
                                border: 0;
                                i {
                                    font-size: 16px;
                                }
                            }
                        }
                    }
                    .right {
                        width:50%;
                        display: flex;
                        align-items: center;
                        justify-content: flex-end;
                        span {
                            color: #B7B7B7;
                            font-size: 12px; 
                        }
                        a {
                            margin-left: 10px;
                            i {
                                color: #B7B7B7;
                                font-size: 13px;                                 
                            }
                        }
                    }
                }                
            }
            &.row2{
                background: #1e1e1e;
                padding: 50px 0 60px 0;
                .container {
                    ul {
                        width: 100%;
                        display: flex;
                        justify-content: space-between;
                        li {
                            width: auto;
                            &:nth-child(1){
                                width: 262.500px;
                                h1 {
                                    height: 50px;
                                    display: flex;
                                    align-items: center;                                    
                                    img {
                                        width: auto;
                                    }
                                }
                                p {
                                   font-size: 14px;
                                   color: #808080;
                                   line-height: 170%;
                                   padding-right: 30px;
                                }
                                @media all and (max-width: 780px) {
                                    p {
                                        padding-right: 0;
                                    }
                                }
                            }

                            &:nth-child(2),
                            &:nth-child(3),
                            &:nth-child(4){
                                dl {
                                    dt {
                                        height: 50px;
                                        display: flex;
                                        align-items: center;
                                        font-size: 12px;
                                        font-weight: 600;
                                        color: #b7b7b7;
                                    }
                                    dd {
                                        line-height: 170%;
                                        a {
                                            color: #808080;
                                            transition: color 0.3s;
                                            &:hover {
                                                color: #b7b7b7;
                                            }
                                        }
                                    }
                                }
                            }

                            &:nth-child(5){
                                width: 272.500px;
                                h2 {
                                    width: 100%;
                                    font-size: 12px;
                                    font-weight: 600;
                                    color: #b7b7b7;
                                    height: 50px;
                                    display: flex;
                                    align-items: center;
                                }
                                ul {
                                    width: 100%;
                                    display: flex;
                                    flex-flow: row wrap;
                                    li {
                                        width: calc(100% / 4);
                                        a {
                                            width: calc(100% - 10px);
                                            display: block;
                                            margin: 5px;
                                            transition: all 0.3s ease-in-out;
                                            position: relative;
                                            img {
                                                width: 100%;
                                                position: relative;
                                                z-index: 1;
                                            }
                                            i {
                                                z-index: 2;
                                                position: absolute;
                                                top: 0;
                                                left: 0;
                                                width: 100%;
                                                height: 100%;
                                                color: #fff;
                                                font-size: 20px;
                                                display: flex;
                                                align-items: center;
                                                justify-content: center;
                                                opacity: 0;
                                                transition: all 0.3s ease-in-out;
                                            }

                                            &:hover {
                                                img {
                                                    filter: brightness(40%);
                                                }
                                                i {
                                                    opacity: 1;
                                                }
                                            }
                                        }
                                    }
                                }
                            }
                        }                       
                    }

                    @media all and (max-width: 1200px ) {
                        ul {
                            width: 100%;
                            flex-flow: row wrap;
                            li {                               
                               &:nth-child(1){
                                 width: 25%;
                               }
                               &:nth-child(2){
                                 width: 25%;
                               }
                               &:nth-child(3){
                                 width: 25%;
                               }
                               &:nth-child(4){
                                 width: 25%;
                               }
                               &:nth-child(5){
                                 width: 100%;
                                 ul {            
                                    flex-flow: row nowrap;        
                                    li {
                                        width: calc(100% / 8)                                       
                                    }
                                 }
                               }
                            }
                        }
                    }

                    @media all and (max-width: 780px ) {
                        ul {
                            width: 100%;
                            flex-flow: row wrap;
                            li {      
                               text-align: center;                                                   
                               &:nth-child(1){
                                 width: 100%;
                                 h1 {
                                    justify-content: center;
                                 }
                               }

                               &:nth-child(2),
                               &:nth-child(3),
                               &:nth-child(4){
                                 width: 100%;
                                    dl {
                                        dt {
                                            justify-content: center; 
                                        }
                                    }    
                               }
                               
                               &:nth-child(5){
                                 width: 100%;
                                 h2 {
                                     justify-content: center; 
                                 }
                                 ul {            
                                    flex-flow: row wrap;        
                                    li {
                                        width: calc(100% / 4)                                       
                                    }
                                 }
                               }
                            }
                        }
                    }                    
                }
            }                     
            &.row3{
                background: #141414;
                padding: 30px 0;                            
                .container {
                    display: flex;  
                    .left {
                        width:50%;
                        p {
                            color: #808080;
                            font-size: 14px;
                        }
                    }
                    .right {
                        width:50%;
                        text-align: right;
                        span {
                            img {
                                width: auto;
                            }
                        }
                    }
                }    
            }                     
            
            @media all and (max-width: 1200px) {
                .container {
                    width: calc(100% - 30px - 30px);
                    transition: all 0.3s;                      
                    margin: 0 30px;              
                }
                &.row1{                     
                    .container {
                        .left {
                            form {
                                input {
                                    width: 200px;
                                }                                   
                            }
                        }
                        
                    }                
                }
            }

            @media all and (max-width: 900px) {

                    &.row1{                     
                        .container {
                            .left {
                                width: 60%;
                                span {
                                    display: none;
                                }
                                form {
                                    width: 100%;
                                    input {
                                        width: 100%;
                                    }                                   
                                }
                            }
                            .right {
                                width:40%;
                            }
                           
                        }                
                    }

                    &.row3{                        
                        .container { 
                            flex-flow: row wrap;
                            .left {
                                width:100%;
                                text-align: center;
                                padding-bottom: 25px;
                                p {
                                    color: #808080;
                                    font-size: 10px;
                                }
                            }
                            .right {
                                width:100%;
                                text-align: center;
                            }
                        }    
                    }                       
            }

            @media all and (max-width: 660px) {

                    &.row1{                     
                        .container {
                            .left {
                                width: 100%;
                                span {
                                    display: none;
                                }
                                form {
                                    width: 100%;
                                    margin-left: 0;
                                    input {
                                        width: 100%;
                                    }                                   
                                }
                            }
                            .right {
                                display: none;
                            }
                           
                        }                
                    }
            }

        }
    }
}