#wrap {
    #header {
        z-index: 3;
        width: 100%;
        height: 70px;
        position: fixed;
        top: 0;
        left: 0;
        background: #fff;
        border-bottom: 1px solid rgba(0,0,0, 0.1);
        padding: 0 35px;
        box-shadow: 0 3px 6px 0 rgba(0,0,0, 0.08);

        display: flex;
        justify-content: space-between;
        align-items: center; // 수직정렬은 높이가 설정되어야 한다.
        
        // transition: all 3s ease-in;
        // transition: all 3000ms ease-in;

        // transition: all 0.6s ease-in;
        transition: all 600ms ease-in;
        // 스크롤 아래로 
        &.on {
            top: -70px;
        }
        &.bg {
            background: transparent; // 배경 투명하게 변경
            top: 0;
            box-shadow: 0 0 0 0 rgba(0,0,0, 0.08);
        }

        // 로고
        .left {
            h1 {
                a {
                    transition: all 0.3s;
                    img {
                        width: auto;
                    }
                    &:hover{
                        opacity: 0.6;
                    }
                }
            }
        }
        
        // GNB
        .center {
            height: 100%;
            width: 100%;
            display: flex;
            align-items: center;
            justify-content: center;            
            #gnb {
                height: 100%;
                > ul {
                    height: 100%;
                    display: flex;
                    > li {
                        height: 100%;
                        padding: 0 20px;
                        // blog 블로그
                        &:nth-child(5){
                            position: relative;
                        }
                        // 메인버튼
                        > a {    
                            display: flex;
                            height: 100%;   
                            align-items: center; 
                            justify-content: center;                    
                            span {
                                overflow: hidden;
                                width:100%;
                                display: block;
                                font-size: 13px;
                                font-weight: 500;
                                color: #232323;
                                padding-bottom: 4px;
                                position: relative;
                                // text-decoration: underline;                                
                                // text-underline-offset: 5px;
                                &::after {
                                    content: '';
                                    position: absolute;
                                    left: 0;
                                    bottom: 0;
                                    display: block;
                                    width: 100%;
                                    height: 1px;
                                    background: #000;
                                    animation: ani-1 0.6s forwards; /* 마우스 아웃하면 우측으로 부드럽게 사라짐 */
                                }
                                @keyframes ani-1 {
                                      0% {left:   0%}
                                    100% {left: 100%}  /* 우측으로 사라짐 */
                                }
                            }  
                            &:hover {
                                span {
                                    &::after {
                                        animation: ani-2 0.3s forwards;
                                    }
                                    @keyframes ani-2 {
                                          0% {left:-100%} 
                                        100% {left:   0%} /* 좌측에서 부드럽게 나타남 */
                                    }
                                }
                            }                         
                            &.on { // 메인버튼 효과 이벤트 
                                span {
                                    &::after {
                                        animation: ani-2 0.3s forwards;
                                    }
                                    @keyframes ani-2 {
                                          0% {left:-100%} 
                                        100% {left:   0%} /* 좌측에서 부드럽게 나타남 */
                                    }
                                }
                            }                         
                        }

                        // 서브메뉴
                        .sub {
                            position: absolute;
                            top: 69px;
                            left: 0;
                            width: 100%;
                            background: #141414;
                            height: 0;  // 슬라이드 업 slideUp(0)
                            opacity: 0;
                            overflow: hidden;
                            transition: all 0s ease-in-out;

                            &.sub1.on {
                                transition: all 0.3s ease-in-out;
                                height: 775.156px;  // 슬라이드 업 slideDown(300)
                                opacity: 1;
                            }
                            &.sub2.on {
                                transition: all 0.3s ease-in-out;
                                height: 663.875px; 
                                opacity: 1;
                            }
                            &.sub3.on {
                                transition: all 0.3s ease-in-out;
                                height: 457.203px; 
                                opacity: 1;
                            }
                            &.sub4.on {
                                transition: all 0.3s ease-in-out;
                                height: 476.656px;
                                opacity: 1;
                            }
                            &.sub5 {
                                width: 180px;
                                &.on{
                                    transition: all 0.3s ease-in-out;
                                    height: 370px;   
                                    opacity: 1; 
                                    overflow: visible;  // 모든 서브메뉴는 오버플로우 숨김
                                                        // 그러나 서브5 서브서브가 있어서 보여라
                                }
                            }
                            &.sub6.on {  // 서브4 공용
                                transition: all 0.3s ease-in-out;
                                height: 476.656px; 
                                opacity: 1;
                            }
                            &.sub7.on {  // 서브4 공용
                                transition: all 0.3s ease-in-out;
                                height: 386.656px; 
                                opacity: 1;
                            }
                            .container {
                                width: 100%;
                                max-width: 15px + 1140px + 15px;
                                margin: 0 auto;

                                // 서브1 ul
                                ul.sub1-ul {
                                     width: 100%;
                                     display: flex;
                                     flex-flow: row wrap;
                                     padding: 30px 0;
                                    > li {
                                         width: calc(100% / 4);
                                        > a {
                                            display: block;
                                            width: calc(100% - 15px - 15px);
                                            margin: 0 15px;
                                            padding: 30px 0;
                                            text-align: center;
                                            img {
                                                width: 100%;
                                                transition: transform 0.3s;
                                            }                                        
                                            strong {
                                                font-size: 13px;
                                                font-weight: 500;
                                                color: #fff;
                                                padding-top: 25px;
                                                display: block;
                                                transition: opacity 0.3s;
                                            }
                                            &:hover {
                                                img {
                                                    transform: scale(0.95);
                                                } 
                                                strong {
                                                    opacity: 0.6;
                                                }
                                            }
                                        }
                                    }
                                }

                                // 서브2 2-1 4칸  2-2 3칸
                                .sub2-row1 {
                                    width: 100%;
                                    padding: 60px 0 30px 0;
                                    > ul {
                                        width: 100%;
                                        display: flex;
                                        > li {
                                            width: calc(100% / 4);
                                            border-right: 1px solid #333;
                                            &:last-child {
                                                border-right: 0;                                                
                                            }
                                            > dl {
                                                width: calc(100% - 30px);
                                                margin: 0 15px;
                                                > dt {
                                                    width: 100%;  
                                                    padding-bottom: 12px;                                                  
                                                    strong {
                                                        color: #fff;
                                                    }
                                                }
                                                > dd {
                                                    width: 100%;
                                                    padding: 5px 0;
                                                    a {
                                                        color: #8d8d8d;
                                                    }
                                                }
                                            }
                                        }
                                    }
                                }
                                .sub2-row2 {
                                    width: 100%;
                                    padding: 30px 0 60px 0;
                                    > ul {
                                        width: 100%;
                                        display: flex;
                                        > li {
                                            width: calc(100% / 3);
                                           > a {
                                                display: block;
                                                width: calc(100% - 30px);
                                                margin: 0 15px;
                                                img {
                                                    width: 100%;
                                                }
                                            }
                                        }
                                    }
                                }

                                .sub3-ul {
                                    width: 100%;
                                    display: flex;
                                    padding: 60px 0;
                                    > li {
                                        width: calc(100% / 4);
                                        border-right: 1px solid #303030;
