                                            dl {
                                                dt {
                                                    padding-bottom: 12px;
                                                    strong {
                                                        font-size: 13px;
                                                        color: #fff; 
                                                        font-weight: 500;
                                                        
                                                    }                                                    
                                                }
                                                dd {
                                                    padding: 9px 0;
                                                    a {
                                                        img {
                                                            width: 32px;
                                                            height: 32px;
                                                            border-radius: 50%;
                                                        }
                                                        span {
                                                            color: #8d8d8d;
                                                            font-size: 12px;
                                                            font-weight: 500;
                                                            transition: color 0.3s;
                                                            margin-left: 15px;
                                                        }
                                                        &:hover {
                                                           span {
                                                                color: #fff;
                                                            } 
                                                        }
                                                    }
                                                }
                                            }
                                        }
                                        &:nth-child(2) {                                            
                                            dl {
                                                width: 100%;
                                                dt {
                                                    padding-bottom: 12px;
                                                    strong {
                                                        font-size: 13px;
                                                        color: #fff; 
                                                        font-weight: 500;                                                        
                                                    }   
                                                }
                                                dd {
                                                    width:100%;
                                                    padding: 15px;
                                                    background: #232323;
                                                    margin: 15px 0;
