                                                                    color: #8d8d8d;
                                                                    transition: color 0.3s;
                                                                    &:hover {
                                                                        color: #fff;
                                                                    }
                                                                }
                                                                span {
                                                                   color: #fd9d38;
                                                                   font-size: 12px;
                                                                }
                                                                em {
                                                                    color: #8d8d8d;
                                                                    font-size: 11px;
                                                                    text-decoration: line-through;
                                                                }
                                                                strong {
                                                                    color: #8d8d8d;
                                                                    font-size: 11px;
                                                                }
                                                            }
                                                        }
                                                    }
                                                }
                                            }
                                        }

                                        &:nth-child(3),
