                                                width: auto;
                                                display: block;
                                                margin: 0 15px;
                                                img {
                                                    width: 100%;
                                                    transition: all 0.3s;
                                                }
                                                &:hover {
                                                    img {
                                                        transform: scale(0.95);
                                                        filter: brightness(70%);
                                                    }
                                                }
                                            }
                                        }
                                    }
                                }    

                                .sub4-ul {
