                                                        color: #fff;
                                                    }
                                                }
                                            }
                                        }

                                        > a {
                                            width: 100%;
                                            display: block;
                                            padding: 0 15px;
                                            img {
                                                width: 100%;
                                                transition: all 0.3s;
                                            }
                                            &:hover {
                                                 img {
                                                    filter: brightness(70%);
                                                    transform: scale(0.95);
                                                }
                                            }
                                        }
                                    }
                                }

                                .sub5-ul {
                                    width: 100%;
                                    padding: 20px 0;
                                    > li {
                                        width: 100%;
                                        padding: 9px 0;
                                        position: relative;
                                        > a{
                                            display: flex;
                                            width: 100%;
                                            padding: 0 22px;
                                            font-size: 12px;
                                            color: #8d8d8d;
                                            transition: color 0.3s;
                                            &:hover {
                                                color: #fff;
                                            }
                                        }
                                        &.sub-sub-line {
                                             a{ 
                                                justify-content: space-between;
                                                i {
                                                    color: #8d8d8d;
                                                    font-size: 10px;   
                                                    transition: color 0.3s;                                                
                                                }
                                                &:hover {
                                                     i {
                                                        color: #fff;                                              
                                                    }
                                                }
                                             }
                                        }

                                        .sub-sub {
                                            background: #141414;                                        
                                            width: 180px;
                                            position: absolute;
                                            top: -20px;
                                            left: 100%;
                                            display: none;
                                            margin-left: 1px;
                                            &.sub5-sub1.on {display: block;}
                                            &.sub5-sub2.on {display: block;}
                                            &.sub5-sub3.on {display: block;}
                                            .container {
                                                width: 100%;
                                                > ul {
                                                    width: 100%;
                                                    padding: 20px 0;
                                                    > li {
                                                         width: 100%;
                                                         padding: 9px 0;
                                                        > a {
                                                            display: block;
                                                            width: 100%;
                                                            font-size: 12px;
                                                            color: #8d8d8d;
                                                            transition: color 0.3s;
                                                            padding: 0 22px;
                                                            &:hover {
                                                                color: #fff;
                                                            }
                                                        }
                                                    }
                                                }
                                            }
                                        }
                                    } 
                                }
                            }
                        }
                    }
                }
            }
            .mobile-btn {
                display: none;
                width: 16px;
                height: 14px;
                position: relative;
                z-index: 3;
                i {
                    width: 100%;
                    height: 2px;
                    background: rgba(0,0,0, 1);
                    position: absolute;
                    left: 0;
                    transition: all 0.3s ease-in-out;
                    &:nth-child(1){
                        top: 0;
                    }
                    &:nth-child(2){
                        top: 50%;
                        margin-top: -1px;
                    }
                    &:nth-child(3){
                        bottom: 0;
                    }
                }

                &.on {
                    i {
                        &:nth-child(1){
                            top: 50%;
                            margin-top: -1px;
                            transform: rotate(45deg);
                        }
                        &:nth-child(2){
                            display: none;
                        }
                        &:nth-child(3){
                            bottom: 50%;
                            margin-bottom: -1px;
                            transform: rotate(-45deg);
                        }
                    }
                }
            }
        }

        // aside
        .right {
            #aside {
                ul {
                    display: flex;
                    li {
                        margin-left: 13px;
                        a {
                            color:#555;
                            transition: all 0.3s;
                            
                            i {
                                font-size: 15px;
                            }
                            &:hover {
                                color: #000;
                            }
                        }
                        &:nth-child(2){
                             a {
                                i {
                                    font-size: 18px;
                                }
                            }
                        }                        
                    }                    
                }
            }
        }


    }

