                                        padding: 0;
                                        flex-flow: row wrap;
                                        > li {
                                            width: calc(100% / 4);
                                            width: calc(100% / 1);
                                            border-right: 1px solid #303030;
                                            border-right: 0;
                                            padding: 0 15px;
                                            &:nth-child(1) {
                                                dl {
                                                    padding: 15px 0;
                                                    dt {
                                                        padding-bottom: 12px;
                                                        strong {
                                                            font-size: 13px;
                                                            color: #fff; 
                                                            font-weight: 500;
                                                            
                                                        }                                                    
                                                    }
                                                    dd {
                                                        padding: 9px 0;
                                                        border-bottom: 1px solid rgba(255, 255, 255, 0.1);
                                                        &:first-child {
                                                            border-top: 1px solid rgba(255, 255, 255, 0.1);
                                                        }
                                                        a {
                                                            img {                                                            
                                                                width: 32px;
                                                                height: 32px;
                                                                border-radius: 50%;
                                                            }
                                                            span {
                                                                color: #8d8d8d;
                                                                font-size: 12px;
                                                                font-weight: 500;
                                                                transition: color 0.3s;
                                                                margin-left: 15px;
                                                            }
                                                            &:hover {
                                                            span {
                                                                    color: #fff;
                                                                } 
                                                            }
                                                        }
                                                    }
                                                }
                                            }
                                            &:nth-child(2) {                                            
                                                dl {
                                                    width: 100%;
                                                    padding: 15px 0;
                                                    dt {
                                                        padding-bottom: 12px;
                                                        strong {
                                                            font-size: 13px;
                                                            color: #fff; 
                                                            font-weight: 500;                                                        
                                                        }   
                                                    }
                                                    dd {
                                                        width:100%;
                                                        padding: 15px;
                                                        background: #232323;
                                                        margin: 15px 0;
                                                        display: flex;
                                                        border-bottom: 1px solid rgba(255, 255, 255, 0.1);
                                                        &:first-child {
                                                            border-top: 1px solid rgba(255, 255, 255, 0.1);
                                                        }
                                                        .left {
                                                            width: 42px;
                                                            a {
                                                                display: block;
                                                                width: 100%;
                                                                img {
                                                                    width: 100%;
                                                                }
                                                            }
                                                        }
                                                        .right {
                                                            padding-left: 15px;
                                                            width: calc(100% - 42px);
                                                            ul {
                                                                width: 100%;
                                                                li {
                                                                    width: 100%;
                                                                    line-height: 120%;
                                                                    a {
                                                                        display: block;
                                                                        width: 100%;
                                                                        font-size: 12px;
                                                                        color: #8d8d8d;
                                                                        transition: color 0.3s;
                                                                        &:hover {
                                                                            color: #fff;
                                                                        }
                                                                    }
                                                                    span {
                                                                    color: #fd9d38;
                                                                    font-size: 12px;
                                                                    }
                                                                    em {
                                                                        color: #8d8d8d;
                                                                        font-size: 11px;
                                                                        text-decoration: line-through;
                                                                    }
                                                                    strong {
                                                                        color: #8d8d8d;
                                                                        font-size: 11px;
                                                                    }
                                                                }
                                                            }
                                                        }
                                                    }
                                                }
                                            }

                                            &:nth-child(3),
                                            &:nth-child(4) {
                                                a {
                                                    img {
                                                        display: none;
                                                    }                                                   
                                                }
                                            }
                                        }
                                    }    

                                    .sub4-ul {
                                        display: flex;
                                        flex-flow: row wrap;
                                        padding: 60px 0;
                                        padding: 0;
                                        > li {
                                            width: calc(100% / 4);
                                            width: calc(100% / 1);
                                            padding: 0 15px;
                                            border-right: 1px solid #303030;
                                            border-right: 0;
                                            &:last-child {
                                                border-right: 0;
                                            }

                                            dl {
                                                width: 100%;
                                                padding: 15px 0;
                                                dt {

                                                    strong {
                                                        font-size: 13px;
                                                        color: #fff; 
                                                        font-weight: 500;   
                                                    }
                                                }
                                                dd  {
                                                    width: 100%;
                                                    padding: 13px 0;
                                                    border-top: 1px solid rgba(255, 255, 255, 0.1);                                                    
                                                    a {
                                                        width: 100%;
                                                        display: block;
                                                        font-size: 12px;
                                                        color: #8d8d8d;
                                                        transition: color 0.3s;
                                                        &:hover {
                                                            color: #fff;
                                                        }
                                                    }
                                                }
                                            }

                                            > a {
                                                width: 100%;
                                                display: block;
                                                padding: 0 15px;
                                                img {
                                                    display: none;
                                                    width: 100%;
                                                    transition: all 0.3s;
                                                }
                                                &:hover {
                                                    img {
                                                        filter: brightness(70%);
                                                        transform: scale(0.95);
                                                    }
                                                }
                                            }
                                        }
                                    }

                                    .sub5-ul {
                                        width: 100%;
                                        padding: 20px 0;
                                        > li {
                                            width: 100%;
                                            padding: 13px 0;
                                            position: relative;
                                            border-top: 1px solid rgba(255, 255, 255, 0.1);
                                            background: #191919;
                                            &:first-child {
                                                border-top: 0;
                                            }
                                            > a{
                                                display: flex;
                                                width: 100%;
                                                padding: 0 22px;
                                                font-size: 12px;
                                                color: #8d8d8d;
                                                transition: color 0.3s;
                                                &:hover {
                                                    color: #fff;
                                                }
                                            }
                                            &.sub-sub-line {
                                                a{ 
                                                    justify-content: space-between;
                                                    i {
                                                        display: none;
                                                    }
                                                }
                                            }

                                            .sub-sub {
                                                background: #141414;                                        
                                                background: #212121;                                   
                                                width: 180px;
                                                width: 100%;
                                                position: absolute;
                                                position: relative;
                                                top: -20px;
                                                top: 0;
                                                left: 100%;
                                                left: 0;
                                                display: none;
                                                display: block;
                                                margin-left: 1px;
                                                margin-left: 0;
                                                margin:15px 0;
                                                &.sub5-sub1.on {display: block;}
                                                &.sub5-sub2.on {display: block;}
                                                &.sub5-sub3.on {display: block;}
                                                .container {
                                                    width: 100%;
                                                    > ul {
                                                        width: 100%;
                                                        padding: 20px 0;                                                        
                                                        padding: 5px 0;                                                        
                                                        > li {
                                                            width: 100%;
                                                            padding: 9px 0;
                                                            padding: 13px 0;
                                                            border-bottom: 1px solid rgba(255, 255, 255, 0.1);                                                            
                                                            &:last-child {
                                                                border-bottom: 0;
                                                            }
                                                            > a {
                                                                display: block;
                                                                width: 100%;
                                                                font-size: 12px;
                                                                color: #8d8d8d;
                                                                transition: color 0.3s;
                                                                padding: 0 22px;                                                               
                                                                &:hover {
                                                                    color: #fff;
                                                                }
                                                            }
                                                        }
                                                    }
                                                }
                                            }
                                        } 
                                    }
                                }
                            }
                        }
                    }
                    &.on {
                        height: 50px * 7; // 메인메뉴 7줄 350픽셀 높이 고정
                        // height: 100vh;
                    }
                }
                .mobile-btn {
                    display: block;
                }
            }
            .right {
                #aside {
                    ul {
                        li {
                            &:nth-child(3){
                                display: none;
                            }
                        }  
                    }
                }
            }
         }
    }
